
* { box-sizing: border-box; }

html { height: 100%; }

body {
  margin: 0;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: top;
  -webkit-align-items: top;
      -ms-flex-align: top;
          align-items: top;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
      -ms-flex-pack: center;
          justify-content: center;
  height: 100%;
}

#app {
  width: 1000px;
  padding: 10px;
  font-family: 'Source Sans Pro', Helvetica, sans-serif;
  color: #2c3e50;
}

#app h1 { text-align: center; }

.clearfix:after {
  content: "";
  display: table;
  clear: both;
}

.green { color: green; }
.red { color: red; }

.Navigation {
  width: 100%;
  padding-left: 0;
  list-style: none;
  height: 60px;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
      -ms-flex-pack: center;
          justify-content: center;
}

.Navigation__item { line-height: 60px; }

.Navigation__item__link {
  padding: 15px;
  text-decoration: none;
  color: #0065AD;
}

.Navigation__item__link--current {
  border: 2px solid #0065AD;
  border-radius: 3px;
}

.Navigation__item:not(:first-child) { margin-left: 20px; }

.Button {
  padding: 15px;
  border-radius: 3px;
  font-size: 1.2rem;
  background-color: #61BD6D;
  text-decoration: none;
  color: #fff;
  text-align: center;
  cursor: pointer;
}

.Button:hover { background-color: #39BA48; }

.Button--block {
  margin-top: 20px;
  display: block;
  margin: 0 auto;
}

.Button--delete { background-color: #D10000; }
.Button--delete:hover { background-color: #AA0000; }



